.item{display:flex;justify-content:flex-start;align-items:center}.item:not(:last-child){margin-bottom:10px}.checkbox{width:18px;height:18px;outline:none;margin:0 5px;transition:all .5s ease}.checkbox:focus{box-shadow:0 0 5px 0 teal}.body{position:relative;z-index:0;display:flex;column-gap:40px;pointer-events:none;width:100%;cursor:default;padding:5px 10px 5px 10px}.body::first-letter{text-transform:uppercase}.body::before{content:"âœŽ";position:absolute;z-index:3;top:10px;left:13px;transform:rotate(90deg);width:13px;height:13px;cursor:pointer;pointer-events:initial}.text{position:relative;z-index:3;width:100%;padding:0 10px;margin:0 0 0 20px;background-color:rgba(0,0,0,0);pointer-events:initial;cursor:text;border:0;outline:none;outline-offset:0;transition:all .5s ease}.text.completed{text-decoration:line-through}.buttons{display:flex;opacity:0;visibility:hidden;transition:opacity .5s ease,visibility .5s ease}.button{position:relative;z-index:3;flex:0 0 70px;background-color:#fff;border:1px solid #888;border-radius:5px;cursor:pointer;pointer-events:initial;padding:0 7px 2px}.button:not(:last-child){margin-right:13px}.text.focus{background-color:#fff;border:1px solid #888;border-radius:5px}.text.focus+.buttons{opacity:1;visibility:visible;text-decoration:none}